<template>
	<ul class="catalog__pagination pagination">
		<li class="pagination__item">
			<a class="pagination__link pagination__link--arrow" :class="{ 'pagination__link--disabled': page == 1 }" href="#"
				aria-label="Следующая страница" @click.prevent="previousPage(page)">
				<svg width="8" height="14" fill="currentColor">
					<use xlink:href="#icon-arrow-left"></use>
				</svg>
			</a>
		</li>
		<li class="pagination__item" v-for="numberPage in pages" :key="numberPage">
			<a href="#" class="pagination__link" :class="{ 'pagination__link--current': numberPage === page }"
				@click.prevent="paginate(numberPage)">
				{{ numberPage }}
			</a>
		</li>

		<li class="pagination__item">
			<a class="pagination__link pagination__link--arrow" :class="{ 'pagination__link--disabled': page == pages }"
				href="#" aria-label="Следующая страница" @click.prevent="nextPage(page)">
				<svg width="8" height="14" fill="currentColor">
					<use xlink:href="#icon-arrow-right"></use>
				</svg>
			</a>
		</li>
	</ul>
</template>

<script>
export default {
	props: ['page', 'perPage', 'elements'],
	model: {
		event: 'paginate',
		prop: 'page'
	},
	computed: {
		pages() {
			return Math.ceil(this.elements / this.perPage)
		}
	},
	methods: {
		paginate(page) {
			this.$emit('paginate', page)
		},
		nextPage(page) {
			if (page < this.pages) this.$emit('paginate', page + 1)
		},
		previousPage(page) {
			if (page > 1) this.$emit('paginate', page - 1)
		}
	}
}

</script>