import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import { API_BASE_URL } from "@/confing";

Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		cartProduct: [],

		cartProductsData: [],
		accessNumber: null,
		orderInfo: null
	},
	mutations: {
		updateOrderInfo(state, orderInfo) {
			state.orderInfo = orderInfo
		},
		resetCart(state) {
			state.cartProduct = [];
			state.cartProductsData = [];
		},
		updateCartProduct(state, { productId, amount }) {
			const item = state.cartProduct.find(item => item.productId === productId);

			if (item) {
				item.amount = amount;
			}
		},
		deleteProductCart(state, productId) {
			state.cartProduct = state.cartProduct.filter(item => item.productId !== productId);
		},
		updateAccessNumber(state, accessNumber) {
			state.accessNumber = accessNumber
		},
		updateCartProductsData(state, items) {
			state.cartProductsData = items
		},
		syncCartProducts(state) {
			state.cartProduct = state.cartProductsData.map((item) => ({
				productId: item.product.id,
				amount: item.quantity,
			}));
		},
	},
	getters: {
		cartDetailProducts(state) {
			return state.cartProduct.map(item => {
				const product = state.cartProductsData.find(p => p.product.id === item.productId).product

				return {
					...item,
					product: {
						...product,
						image: product.image.file.url
					}
				}
			})
		},
		cartTotalPrice(state, getters) {
			return getters.cartDetailProducts.reduce((acc, item) => (item.product.price * item.amount) + acc, 0)
		},
		cartTotalNumber(state, getters) {
			return getters.cartDetailProducts.length
		}
	},
	actions: {
		loadOrderInfo(context, oredrId) {
			return axios
				.get(API_BASE_URL + '/api/orders/' + oredrId, {
					params: {
						userAccessKey: context.state.accessNumber
					}
				})
				.then((response) => {
					context.commit('updateOrderInfo', response.data)
				})
		},
		loadCart(context) {
			return axios
				.get(API_BASE_URL + '/api/baskets', {
					params: {
						userAccessKey: context.state.accessNumber
					}
				})
				.then(response => {
					if (!context.state.accessNumber) {
						localStorage.setItem('userAccessKey', response.data.user.accessKey);
						context.commit('updateAccessNumber', response.data.user.accessKey);
					}

					context.commit('updateCartProductsData', response.data.items);
					context.commit('syncCartProducts');
				})
		},
		addProductToCart(context, { productId, amount }) {
			return (new Promise(resolve => setTimeout(resolve, 200)))
				.then(() => {
					return axios
						.post(API_BASE_URL + '/api/baskets/products', {
							productId: productId,
							quantity: amount
						}, {
							params: {
								userAccessKey: context.state.accessNumber
							}
						})
						.then(response => {
							context.commit('updateCartProductsData', response.data.items);
							context.commit('syncCartProducts');
						})
				})
		},
		updateCartProductAmount(context, { productId, amount }) {
			context.commit('updateCartProduct', { productId, amount });

			if (amount < 1) {
				return '';
			}

			return axios.put(API_BASE_URL + '/api/baskets/products', {
				productId,
				quantity: amount,
			}, {
				params: {
					userAccessKey: context.state.accessNumber
				}
			})
				.then(response => {
					context.commit('updateCartProductsData', response.data.items);
				})
				.catch(() => {
					context.commit('syncCartProducts');
				})
		},
		deleteCartProduct(context, productId) {
			context.commit('deleteProductCart', productId);

			return axios.delete(`${API_BASE_URL}/api/baskets/products`, {
				data: {
					productId,
				},
				params: {
					userAccessKey: context.state.accessNumber,
				},
			})
				.then((response) => {
					context.commit('updateCartProductsData', response.data.items);
				})
				.catch(() => {
					context.commit('syncCartProducts');
				});
		}
	}
});