<template>
	<main class="content container">
		<div class="content__top content__top--catalog">
			<h1 class="content__title">
				Каталог
			</h1>
			<span class="content__info">
				152 товара
			</span>
		</div>

		<div class="content__catalog">
			<ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo"
				:category-id.sync="filterCategoryId" :color-id.sync="filterColorId" />

			<div v-if="loadingProducts">loading...</div>
			<div v-if="loadingError">Error <button @click="loadProducts">try again</button> </div>

			<section class="catalog">
				<ProductList :products="products" />

				<BasePagination v-model="page" :per-page="productsPerPage" :elements="elements" />
			</section>

		</div>
	</main>
</template>

<script>
import ProductList from '@/components/ProductList';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/confing';

export default {
	components: { ProductList, BasePagination, ProductFilter },
	data() {
		return {
			filterPriceFrom: 0,
			filterPriceTo: 0,
			filterCategoryId: 0,
			filterColorId: 0,

			page: 1,
			productsPerPage: 4,

			loadingProducts: false,
			productsData: null,
		}
	},
	computed: {
		products() {
			return this.productsData
				? this.productsData.items.map(product => {
					return {
						...product,
						image: product.image.file.url
					}
				})
				: [];
		},
		elements() {
			return this.productsData ? this.productsData.pagination.total : 0
		}
	},
	methods: {
		loadProducts() {
			this.loadingProducts = true;
			clearTimeout(this.loadProductsTimer);
			this.loadProductsTimer = setTimeout(() => {
				axios.get(API_BASE_URL + `/api/products`, {
					params: {
						page: this.page,
						limit: this.productsPerPage,
						categoryId: this.filterCategoryId,
						colorId: this.filterColorId,
						minPrice: this.filterPriceFrom,
						maxPrice: this.filterPriceTo
					}
				})
					.then(response => this.productsData = response.data)
					.then(this.loadingProducts = false)
			}, 0);
		}
	},
	created() {
		this.loadProducts()
	},
	watch: {
		page() {
			this.loadProducts()
		},
		filterCategoryId() {
			this.loadProducts()
		},
		filterPriceFrom() {
			this.loadProducts()
		},
		filterPriceTo() {
			this.loadProducts()
		},
		filterColorId() {
			this.loadProducts()
		}
	}

}
</script>